/* a11y */
.swiper {
	.swiper-notification {
		position: absolute;
		left: 0;
		top: 0;
		pointer-events: none;
		opacity: 0;
		z-index: -1000;
	}
}
.swiper-free-mode {
	>.swiper-wrapper {
		transition-timing-function: ease-out;
		margin: 0 auto;
	}
}
.swiper-grid {
	>.swiper-wrapper {
		flex-wrap: wrap;
	}
}
.swiper-grid-column {
	>.swiper-wrapper {
		flex-wrap: wrap;
		flex-direction: column;
	}
}
.swiper-fade {
	.swiper-slide {
		pointer-events: none;
		transition-property: opacity;
		.swiper-slide {
			pointer-events: none;
		}
	}
	.swiper-slide-active {
		&, .swiper-slide-active {
			pointer-events: auto;
		}
	}
  &.swiper-free-mode {
    .swiper-slide {
      transition-timing-function: ease-out;
    }
}
}
.swiper-cube, .swiper-flip{
	overflow: visible;
	.swiper-slide {
		pointer-events: none;
		backface-visibility: hidden;
		z-index: 1;
		.swiper-slide {
			pointer-events: none;
		}
	}
	.swiper-slide-shadow-top, .swiper-slide-shadow-bottom, .swiper-slide-shadow-left, .swiper-slide-shadow-right {
		z-index: 0;
		backface-visibility: hidden;
	}
	.swiper-slide-active {
		&, .swiper-slide-active {
			pointer-events: auto;
		}
	}
}
.swiper-cube {
	.swiper-slide {
		visibility: hidden;
		transform-origin: 0 0;
		width: 100%;
		height: 100%;
	}
	.swiper-slide-active {
		visibility: visible;
	}
	.swiper-slide-next {
		pointer-events: auto;
		visibility: visible;
		+ {
			.swiper-slide {
				pointer-events: auto;
				visibility: visible;
			}
		}
	}
	.swiper-slide-prev {
		pointer-events: auto;
		visibility: visible;
	}
	.swiper-cube-shadow {
		position: absolute;
		left: 0;
		bottom: 0px;
		width: 100%;
		height: 100%;
		opacity: 0.6;
		z-index: 0;
		&::before {
			content: '';
			background: #000;
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			right: 0;
			filter: blur(50px);
		}
	}
  &.swiper-rtl {
    .swiper-slide {
      transform-origin: 100% 0;
    }
  }
}
.swiper-creative {
	.swiper-slide {
		backface-visibility: hidden;
		overflow: hidden;
		transition-property: transform, opacity, height;
	}
}
.swiper-cards {
	overflow: visible;
	.swiper-slide {
		transform-origin: center bottom;
		backface-visibility: hidden;
		overflow: hidden;
	}
}



